import {useRef} from 'react'
import emailjs from '@emailjs/browser';
import {Button} from 'react-bootstrap';

const Kontakt = () => {
  const form = useRef()
  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm('service_w9zq777', 'template_nbjbu2s', form.current, {
        publicKey: 'P_x3RDuj3CPAMPows',
      })
      .then(
        () => {
          console.log('SUCCESS!');
        },
        (error) => {
          console.log('FAILED...', error.text);
        },
      );
      e.target.reset()
  };
  return (
    <section>
      <div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column', paddingTop: '40px' }}>
        <h1>Kontaktieren sie uns</h1>
        <form ref={form} onSubmit={sendEmail} style={{ display: 'flex', flexDirection: 'column', width: '80%', maxWidth: 600, marginTop: 20 }}>
            <input type='text' placeholder='full Name' name='user_name' style={{ marginTop: 10, height: 40 }} required/>
            <input type='email' placeholder='Email' name='user_email' style={{ marginTop: 10, height: 40 }} required/>
            <input type='text' placeholder='Subject' name='subject' style={{ marginTop: 10, height: 40 }} required/>
            <textarea name='message' cols='30' rows='10' style={{ marginTop: 10, height: 40 }}></textarea>
            <Button variant='primary' type='submit' style={{ marginTop: 10, height: 40 }}>Send Message</Button>
        </form>
      </div>
    </section>
  )
}

export default Kontakt