import React from 'react';

const Ort = () => {
    return (
      <div className='map'>
        <div className='map-content'>
          <iframe
            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2491.8609345052214!2d8.9866574!3d49.462665199999996!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x4797ef6f1c41517d%3A0x74fd2b69c299787f!2sGentlemen%E2%80%98s%20Barbershop!5e1!3m2!1sde!2sde!4v1723785551509!5m2!1sde!2sde"
            width="100%"
            height="100%"
            style={{ border: 0 }}
            allowFullScreen
            loading="lazy"
            referrerPolicy="no-referrer-when-downgrade"
            title="Google Maps"
          ></iframe>
        </div>
      </div>
    );
}

export default Ort;
