import React from 'react';
import { useNavigate } from 'react-router-dom';
import Col from 'react-bootstrap/Col';
import Image from 'react-bootstrap/Image';
import {Button} from 'react-bootstrap';
import {HOME_ROUTE, KONTAKT_ROUTE, ORT_ROUTE} from '../const.js';

const Navbar = () => {
  const navigate = useNavigate();
    return (
      <div>
        <div className='Start'>
            <Col xs={6} md={4}>
                <Image className='imgBarber' src="/img/BarberLogo.jpg" roundedCircle />
            </Col>
            <div className='btnlink'>
                <Button id='btnNav' onClick={() => navigate(HOME_ROUTE)} variant='outline-light'>Home</Button>
                <Button id='btnNav' onClick={() => navigate(KONTAKT_ROUTE)} variant='outline-light'>Kontakt</Button>
                <Button id='btnNav' onClick={() => navigate(ORT_ROUTE)} variant='outline-light'>Ort</Button>
            </div>
        </div>
      </div>
    );
}

export default Navbar;