import React, { Component, createRef } from 'react';
import Image from 'react-bootstrap/Image';
import Card from 'react-bootstrap/Card';
import {Button} from 'react-bootstrap';


class Start extends Component {
    constructor(props){
        super(props);
        this.state = {
            moreInformations1: false,
            moreInformations2: false,

        }
        this.btn1Ref = createRef()
    }

    changeInf1 = (Nr) => {
        this.setState(prevState => ({
            moreInformations1: !prevState.moreInformations1
        }), () => {
            this.btn1Ref.current.innerHTML = this.state.moreInformations1 ? 'Weniger Informationen' : 'Mehr Informationen';
        });
    }

    changeInf2 = (show) => {
        this.setState({ moreInformations2: show})
    }

    render() { 
        return (
            <div>
                <div className='Card'>
                    <Card className='card-item'>
                        <Card.Img variant="top" src="/img/Haarschnitt.jpg" />
                        <Card.Body>
                        <Card.Title style={{ fontSize: '30px' }}>Haarschnitt</Card.Title>
                        <Card.Text style={{ fontSize: '20px' }}>
                            Preis: 20 Euro
                        </Card.Text>
                        {this.state.moreInformations1 && <Card.Text style={{ fontSize: '20px' }}>
                            bei uns bekommen sie den besten Haarschnitt, so wie sie es sich wnschen.
                        </Card.Text>}

                        <Button ref={this.btn1Ref} onClick={() => this.changeInf1()} variant="primary" style={{ fontSize: '20px' }}>Mehr Informationen</Button>
                        </Card.Body>
                    </Card>
                    <Card className='card-item'>
                        <Card.Img variant="top" src="/img/Bart.jpg" />
                        <Card.Body>
                        <Card.Title style={{ fontSize: '30px' }}>Bartrasur</Card.Title>
                        <Card.Text style={{ fontSize: '20px' }}>
                            Preis: 14 Euro
                        </Card.Text>
                        <Button onClick={() => this.changeInf2(true)} variant="primary" style={{ fontSize: '20px' }}>Mehr Informationen</Button>
                        </Card.Body>
                    </Card>
                    <Card className='card-item'>
                        <Card.Img variant="top" src="/img/Haarschnitt.jpg" />
                        <Card.Body>
                        <Card.Title style={{ fontSize: '30px' }}>Haarschnitt</Card.Title>
                        <Card.Text style={{ fontSize: '20px' }}>
                            Preis: 20 Euro
                        </Card.Text>
                        {this.state.moreInformations1 && <Card.Text style={{ fontSize: '20px' }}>
                            bei uns bekommen sie den besten Haarschnitt, so wie sie es sich wnschen.
                        </Card.Text>}

                        <Button ref={this.btn1Ref} onClick={() => this.changeInf1()} variant="primary" style={{ fontSize: '20px' }}>Mehr Informationen</Button>
                        </Card.Body>
                    </Card>
                    <Card className='card-item'>
                        <Card.Img variant="top" src="/img/Bart.jpg" />
                        <Card.Body>
                        <Card.Title style={{ fontSize: '30px' }}>Bartrasur</Card.Title>
                        <Card.Text style={{ fontSize: '20px' }}>
                            Preis: 14 Euro
                        </Card.Text>
                        <Button onClick={() => this.changeInf2(true)} variant="primary" style={{ fontSize: '20px' }}>Mehr Informationen</Button>
                        </Card.Body>
                    </Card>
                </div>

                {this.state.moreInformations2 && (
                    <div id='moreInf'>
                        <div id='moreInf-content' ref={this.changeBgRef}>
                            <span className="close" onClick={() => this.changeInf2(false)}>&times;</span>
                        
                        </div>
                    </div>
                )}

                <div className='bottom'>
                    <div style={{display: 'flex', alignItems: 'center', marginBottom: '30px', marginTop: '20px'}}>
                        <Image src="/img/Telefon.png" />
                        <p>Telefon: 0000000</p>
                    </div>
                    <div style={{display: 'flex', alignItems: 'center', marginBottom: '30px', marginTop: '20px'}}>
                        <Image src="/img/Ort.png" />
                        <a href='https://www.google.com/maps/place/Gentlemen%E2%80%98s+Barbershop/@49.4626652,8.9866574,572m/data=!3m2!1e3!4b1!4m6!3m5!1s0x4797ef6f1c41517d:0x74fd2b69c299787f!8m2!3d49.4626652!4d8.9866574!16s%2Fg%2F11vbtzfkpg?entry=ttu'>Ort: Strae/Nummer</a>
                    </div>
                    <div style={{display: 'flex', alignItems: 'center', marginBottom: '30px', marginTop: '20px'}}>
                        <Image src="/img/Email.png" />
                        <p>Emailadresse: gmx.de</p>
                    </div>
                </div>
            </div>
        );
    }
}

export default Start;